/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState } from 'react'

import './Jumbotron.scss'


const Jumbotron = ({data}) => {
  const [ opened, setOpened] = useState(false)
  const styles = opened ? 'opened' : ''

  return (
      <div className="jumbotronContainer">
        <div className="jumbotronContainerBefore"></div>
        <div className="container-fluid">
          <div className="row">
            <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
              <div className="jumbotronHeader">
                <h5 className="jumbotronDescription">
                  {`${data.date}`}
                </h5>
                <h2 className="jumbotronTitles">
                  {`${data.title}`}
                </h2>
              </div>

              <div className="jumbotronBody">
                <p>
                  { opened ? `${data.body}` : `${data.body.substring(0,255)}...`}
                </p>
                <p>
                  {`${data.author}`}
                </p>
              </div>

              <footer className="jumbotronFooter">
                <a 
                {...{
                  className: `btn btn-primary ${styles}`, 
                  role: "button",
                  onClick: () => { setOpened(!(opened))}

                }}                
                >
                  {!opened ? 'Read More' : 'Read Less'} 
                <i className="fa fa-long-arrow-right" aria-hidden="true"></i>
                </a>
              </footer>

            </div>
      
          </div>
        </div>
      </div>
  )
}     

export default Jumbotron