import React, { useState } from 'react'
import { withRouter } from 'react-router'

import './Work.scss'
import CategoryList from './CategoryList'

const Work = () => {
  const [category, onCategoryChange] = useState("all")

  const WorkItem = ({data, category}) => {
    const styles = (category === data.category || category === "all") ? "show" : "hide"
    return (
      <li className={`team shot-thumbnail ${data.category} ${styles}`}>
        <div className="multi-shot">
          <div className="dribbble-img">
            <a href className="dribbble-link">
              <picture>
                <img alt="images-5" src={data.image} loading="lazy"/>
              </picture>
            </a>
            <a className="dribbble-over" href={data.url} loading="lazy" data-lightbox="mygallery" data-title="Dribbble">
              <strong className="hover-text">{data.url}</strong>
            </a>
            <a href={data.url} className="dribbble-title">
              <h3>{data.name}</h3>
            </a>
          </div>
        </div>
      </li>
    )
  }


  const filtered_data = CategoryList.map((data) => {
    return <WorkItem key={data.name} data={data} category={category} />
  })

  return (

    <div className="container-fluid filterbox">
      <div className="filter-tabs">
        <div className="filter-sort">
          <div className="navigation">
            <a href onClick={() => onCategoryChange("all")} className="button active">Hemmesi</a>
            <a href onClick={() => onCategoryChange("Service")} className="button">Hyzmatlar</a>
            <a href onClick={() => onCategoryChange("Software")} className="button">Programma pjniligi</a>
          </div>
        </div>
      </div>
      <div className="main-wrap wrap-inner">
        <div id="content">
          <div className="main-full" id="main">
            <ol className="content">

              {filtered_data}

            </ol>
          </div>
        </div>
      </div>
    </div>

  )
}

export default withRouter(Work)