import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'

import Jumbotron from '../../Jumbotron'
import Preloader from '../../Preloader'

const News = ({apiService}) => {

	const [media_data, setMediaData] = useState([])
	useEffect(() => {
		const getdata = async () => {
			const response_data = await apiService.getMedia()
			setMediaData(response_data)
		}
		getdata()
	}, [apiService])

	const data = media_data.map((data) => (
		<Jumbotron key={data.id} data={data} />
		))

	const loader = media_data.length === 0 ? <Preloader /> : null

	return (
		<>
			{loader}
			{data}
		</>
	)
}


export default withRouter(News)