import React from 'react'
import { withRouter } from 'react-router'

import './FooterDockElement.scss'

const FooterDockElement = ({ setTitle , history, data}) => {
  return (
    <div className="dock_element"
      onClick={() => {
        setTitle(data.title)
        history.push(data.url)
      }
    }>
      {data.icon}
      <span className="dock_text">{data.name}</span>
    </div>
  )
}

export default withRouter(FooterDockElement)