import React from 'react'
import { withRouter } from 'react-router'
import { useTranslation } from 'react-i18next'

import getCategoryDockList from './getCategoryDockList'
import FooterDockElement from '../FooterDockElement'
import './Footer.scss'

const Footer = ({setTitle}) => {

  const {t} = useTranslation()
  const CategoryDockList = getCategoryDockList(t)
  return (
    <nav className="nav">
      {
        CategoryDockList.map((data) => {
          return (
            <FooterDockElement key={data.name} setTitle={setTitle} data={data} />
          )
        })
      }

    </nav>
  )
}

export default withRouter(Footer)