import React, {useState} from 'react'

import {
	withRouter
	}	from 'react-router-dom'

import './Modal.scss'


const Modal = ({modalState, children, history, title}) => {

	const classes = modalState ? 'ModalShow' : null
	const [animationStyle, setAnimationStyle] = useState(null)
	const overlayState = modalState ? 'visible' : null
	
	const onModalClose = () =>{
		document.getElementsByClassName('ModalContent')[0].scrollTo({
			top: 0,
			behavior: "auto"
		})
		setAnimationStyle('closeAnimation')
		setTimeout(() => {
			setAnimationStyle(null)
			history.push('/')
		}, 500);
	}

	return (
		<div className={`Modal ${classes} ${animationStyle}`}>
			<div className={`overlay ${overlayState}`}
				onClick={() => onModalClose()}></div>
			<div className="ModalContent">
				<div className="ModalHeader">
					<span
						className="close-btn"
						onClick={() => onModalClose()}>
						&times;
					</span>
					<h1>{title}</h1>
				</div>
				<div className="ModalBody">
					{
						React.Children.map(children, (child) => {
							return React.cloneElement(child)
						})
					}
				</div>
			</div>
		</div>
	)
}

export default withRouter(Modal)