import React, {useState, useEffect} from 'react'
import {
Route,
Switch,
withRouter,
}	from 'react-router-dom'
import { useTranslation } from 'react-i18next'

import Logo from '../Logo'
import Boxes from '../Boxes'
import Particle from '../Particle'

import Modal from '../Modal'

import {
  Sapchozgut,
  Akhasap,
  Saphasap,
  LS,
  Hilli,
  Servicesap,
  PriceChecker,
	Sapsargyt,
  Contact,
} from '../RoutePages'

import Footer from '../Footer'
import './MainView.scss'
import './Responsive.scss'
import LangButton from '../LangButton/LangButton'
import getTitle from './getTitle'
import { useHeader } from '../HeaderProvider'


const MainView = ({history}) => {
	const {t} = useTranslation()

	var current_path = history.location.pathname
	const [modalTitle, setTitle] = useState('Sap zgt')
	const page_data_list = getTitle(t)
	const [modalState, setModalState] = useState(current_path === '/' ? false : true)
	const [dropdownState, onDropdownStateChange] = useState(false)
	const { onHeaderUpdate } = useHeader()

	useEffect(() => {
		setModalState(current_path === '/' ? false : true)

		let pageData = fetchPageData(page_data_list, current_path)
		setTitle(pageData ? pageData.title : "Sap zgt")

		onHeaderUpdate({...pageData})
	}, [current_path, modalTitle])

	const fetchPageData = (page_data_list, current_path) => {
		var pageData = null;
		page_data_list.map((page_data) => {
			if (page_data.path === current_path){
				pageData = page_data
			}
			return true
		})
		return pageData
	}
	
	return (
		<div className="container">
			{modalState === false ? <Particle /> : null}
				<LangButton
					onDropdownStateChange={onDropdownStateChange}
					dropdownState={dropdownState} />

			<div className="row">

				<Switch>
					<div>
						<Modal
							title={modalTitle}
							modalState={modalState} >

							<Route path="/sapcozgut"
								render={() => <Sapchozgut />} />

							<Route path="/sapsargyt"
								render={() => <Sapsargyt />} />

							<Route path="/pricechecker"
								render={() => <PriceChecker />} />

							<Route path="/akhasap"
								render={() => <Akhasap />} />

							<Route path="/saphasap"
								render={() => <Saphasap />} />

							<Route path="/hilli"
								render={() => <Hilli />} />

							<Route path="/lomaysowda"
								render={() => <LS />} />

							<Route path="/service"
								render={() => <Servicesap />} />

							<Route path="/contact"
								render={() => <Contact />} />

						</Modal>
					</div>
				</Switch>

				<div className="col-xl-6 col-lg-6 col-md-6 col-sm-12 col-12">
					<Logo />
				</div>
				<div className="col-xl-6 col-lg-6 col-md-6 col-sm-12 col-12">
					<div className="BoxContainer">
						<Boxes
							setTitle={setTitle}
							/>
					</div>
				</div>
				<Footer
					setTitle={setTitle}
					/>
			</div>
		</div>
	)
}

export default withRouter(MainView)