import React from 'react'
import { useTranslation } from 'react-i18next'
import Typewriter from 'typewriter-effect'


import LogoImage from './images/sap_logo.png'
import './Logo.scss'

const Logo = () => {
	const {t} = useTranslation()
	const text1 = t('typewriter.automation')
	const text2 = t('typewriter.mobile_app')
	const text3 = t('typewriter.web_app')
	const text4 = t('typewriter.service')


	return (
		<div className="Logo">
			<img src={LogoImage} alt="Company logo"/>
			<div className="text_cont">
				<Typewriter
					options={{
						strings: [
							text1,
							text2,
							text3,
							text4],
						autoStart: true,
						loop: true,
						delay: 35,
						deleteSpeed: 20
					}}
				/>
			</div>
		</div>
	)
}

export default Logo