import React from 'react'
import { withRouter }	from 'react-router-dom'

import MainView from '../MainView'
import AppsView from '../AppsView'

const AppGateway = ({history}) => {

	var current_path = history.location.pathname
	const app_route_prefix = process.env.REACT_APP_APPS_VIEW_URL_PREFIX
	const main_view_active = !current_path.includes(app_route_prefix) ? true : false

	const current_view = main_view_active ? <MainView /> : <AppsView />

	return (
		current_view
	)

}

export default withRouter(AppGateway)