import React, { useState } from 'react'
import { useTranslation } from 'react-i18next'

import { withRouter } from 'react-router'
import getCollapseData from './getCollapseData'

import './Collapse.scss'

const Accordion = () => {
  const {t} = useTranslation()
  const CollapseData = getCollapseData(t)
  return(
    <div {...{ className: 'wrapper' }}>
        <ul {...{ className: 'accordion-list' }}>
          {CollapseData.map((data, key) => {
            return (
              <li {...{ className: 'accordion-list__item', key }}>
                <AccordionItem {...data} />
              </li>
            )
          })}
        </ul>
      </div>
  )
}

const AccordionItem = ({ paragraph , title }) => {

  const [opened, setOpened] = useState(false)
  var description;

    if (typeof(paragraph) === "object"){
      description = paragraph.map((item) => {
        return <p>{item}</p>
      })
    }
    else {
      description = paragraph;
    }

  return (
    <div
      {...{
        className: `accordion-item, ${opened && 'accordion-item--opened'}`,
          onClick: () => { setOpened(!opened) }
        }}
      >
        <div {...{ className: 'accordion-item__line' }}>
          <h3 {...{ className: 'accordion-item__title' }}>
            {title}
          </h3>
          <span {...{ className: 'accordion-item__icon' }}/>
        </div>
          <div {...{ className: 'accordion-item__inner' }}>
            <div {...{ className: 'accordion-item__content' }}>
              <p {...{ className: 'accordion-item__paragraph' }}>
                { description }
              </p>
            </div>
          </div>
      </div>
  )
}

export default withRouter(Accordion)