class SapApiService {
  _apiUrl = "https://saphasap.com"
  _apiPrefix = "/sap/api/"

  getApiData = async (url) => {
    const response = await fetch(`${this._apiUrl}${this._apiPrefix}${url}`)
    if (!response.ok) {
      throw new Error(`Could not fetch ${url}, received ${response.status}`)
    }
    return await response.json()
  }

  getMedia = async () => {
    const response = await this.getApiData(`v1/tbl-media/`)
    return response.data.map(this._transformMedia)
  }

  _transformMedia = (data) => {
    return {
      id: data.MediaId,
      author: data.MediaAuthor,
      date: data.MediaDate,
      body: data.MediaBody,
      title: data.MediaTitle,
      url: data.MediaUrl
    }
  }
}

export default SapApiService