import React from 'react'
import {
	Route,
	Switch,
	withRouter,
	}	from 'react-router-dom'

import News from '../RoutePages/News'
import SapApiService from '../../services/SapApiService'


const AppsView = () => {
	const {
		REACT_APP_APPS_VIEW_URL_PREFIX,
		REACT_APP_NEWS_APP_VIEW_URL_PREFIX
	} = process.env

	const apiService = new SapApiService()

	return (
		<>
			<Switch>
				<Route path={`${REACT_APP_APPS_VIEW_URL_PREFIX}${REACT_APP_NEWS_APP_VIEW_URL_PREFIX}`}
					render={() => <News apiService={apiService}/>} />
			</Switch>
		</>
	)
}

export default withRouter(AppsView)